@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@400;700&family=Grandstander:ital,wght@0,100;0,400;0,600;0,700;1,100;1,400;1,600;1,700&family=Monda:wght@400;700&family=Nunito+Sans:ital,wght@0,200;0,400;0,700;1,200;1,400;1,700&display=swap);
  /*Títulos: font-family: 'Baloo Tammudu 2', cursive;
    Body: font-family: 'Nunito Sans', sans-serif; 
    font-family: 'Monda', sans-serif;
    blue: rgba(41, 120, 160, 1)
    green: rgb(168, 198, 134) 
    red: rgb(167, 29, 49)*/

:root{
  --blue: rgba(41, 120, 160, 1);
  --bluelight: rgba(41, 120, 160, .6);
  --green: rgba(168, 198, 134, 1);
  --greenlight: rgba(168, 198, 134, .6);
  --greendark: #588157;
  --red: rgb(167, 29, 49);
}

body {
  margin: 0;
  font-family: 'Nunito Sans', sans-serif;
}


/* Loder */
.loader{text-align: center;}
.loader span{display: inline-block;vertical-align: middle;width: 10px;height: 10px;margin: 50px auto;background:rgba(168, 198, 134, 1);background:var(--green);border-radius: 50px;-webkit-animation: loader 0.9s infinite alternate;-moz-animation: loader 0.9s infinite alternate;}
.loader span:nth-of-type(2){background: rgba(168, 198, 134, 1);background: var(--green);-webkit-animation-delay: 0.3s;-moz-animation-delay: 0.3s;}
.loader span:nth-of-type(3){background: rgba(168, 198, 134, 1);background: var(--green);-webkit-animation-delay: 0.6s;-moz-animation-delay: 0.6s;}
@-webkit-keyframes loader{
0%{
	width: 10px;
	height: 10px;
	opacity: 0.9;
	-webkit-transform: translateY(0);
}
100%{
    width: 24px;
    height: 24px;
    opacity: 0.1;
    -webkit-transform: translateY(-21px);
}
}


/* Home page */

.homepage{
  display: flex;
  width: 100%;
  height: 100%;
}

.homepage>:nth-child(1){
  background-color: rgba(168, 198, 134, 1);
  background-color: var(--green);
  width: 40%;
  height: 100vh;
}

.homepage>:nth-child(1) h1{
  font: 90px 'Baloo Tammudu 2', cursive;
  color: white;
  margin: 0;
  line-height: 1;
  padding: 6vh 10vw 0;
}

.homepage>:nth-child(1) h2 {
  color: white;
  text-align: center;
  padding-bottom: 4vh;
}

.homepage img{
  width: 100%;
  height: 50%;
}


.homepage>:nth-child(2){
  border: 10px rgba(168, 198, 134, 1) solid;
  border: 10px var(--green) solid;
  border-radius: 15px;
  padding: 4vw;
  width: 23%;
  height: auto;
  margin: auto;
}

.homepage>:nth-child(2) h2{
  font: 40px 'Baloo Tammudu 2', cursive;
  line-height: 1;
  margin: 0 0 2vh;
}

.homepage label,
.homepage input{
  font: 21px 'Nunito Sans', sans-serif;
  height: 23px;
}

.homepage input,
.homepage form button{
  border-radius: 8px;
  border: 2px solid rgba(168, 198, 134, 1);
  border: 2px solid var(--green);
  width: 100%;
  margin: 2% 0 5%;
}

.homepage button{
  background-color: rgba(168, 198, 134, 1);
  background-color: var(--green);
  color: white;
  padding: 8px;
  height: auto;
  font-size: 18px;
}

.homepage div:nth-child(2) div{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
  padding: 10px 0;
}

.homepage form p{
  color: red;
  font-size: 18px;
}

.homepage div:nth-child(2) a{
  font-size: 20px;
  margin: 1vw;
  text-decoration: none;
}
.homepage div:nth-child(2) a:nth-child(1){
  color: #DB4437;
}

.homepage div:nth-child(2) a:nth-child(2){
  color: #4267B2;
}

/* Change form signup/login */

.homepage div:nth-child(2) div>button{
  border: none;
  border-radius: 10px;
  padding: 10px;
  font-size: 16px;
  height: 40px;
}


.homepage hr{
  border: 1px rgba(168, 198, 134, 1) solid;
  border: 1px var(--green) solid;
}


/* Layout navbar */

nav{
  display: flex;
  background-color: rgba(168, 198, 134, 1);
  background-color: var(--green);
  padding: 1vw;
  color: white;
  font-size: 25px;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 60px;
}

nav a{
  color: white;
  text-decoration: none;
}

nav div:nth-child(1) img{
  width: 35px;
}

nav div:nth-child(1) p{
  margin: 0;
  font-size: 30px;
}

nav ul{
  display: flex;
  justify-content: flex-end;
  margin-right: 5vw;
  align-items: center;

}

nav ul li{
  list-style: none;
  margin: 0;
}

nav div:nth-child(2){
  width: 40%;
}

nav ul img{
  width: 40px;
  border-radius: 50%;
}

.navicon{
  display: flex;
  align-items: center;
}

nav ul li ul{
  visibility: hidden;
  display: none;
  position: absolute;
  right: .2vw;
  top: 70px;
  padding: 3vh 1vw;
}

ul li:hover > ul,
ul li ul:hover {
  visibility: visible;
  display: block;
  background-color: rgba(168, 198, 134, 1);
  background-color: var(--green);
  border-radius: 2px;
}

/* User's home */

.userhome{
  padding-top: 60px;
  display: flex;
  justify-content: space-between;
}

.userhome>div:nth-child(1){
  background-color: rgba(168, 198, 134, .6);
  background-color: var(--greenlight);
  padding: 4vw;
  width: 15%;
  color: white;
  min-height: 50vh;
}

.userhome>div:nth-child(1) li{
  list-style: none;
}

/* User's home book's cards */

.userhome>div:nth-child(2){
  padding: 4vw;
  width: 80%;
}

.userhome>div:nth-child(2)>div:nth-child(2){
  display: flex;
  flex-wrap: wrap;

}

.bookcard{
  width: 200px;
  border-radius: 10px;
  overflow: hidden;
  margin: 1vw;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
  height: 450px;
}

.bookcard img{
  width: 100%;
  height: 30vh;
  object-fit: cover;
  border-bottom: 10px rgba(168, 198, 134, 1) solid;
  border-bottom: 10px var(--green) solid;
}

.bookcard div:nth-child(2){
  padding: 8px;
  height: 30%;
}

.bookcard button{
  padding: 8px;
  width: 90%;
  position: absolute;
  bottom: 10px;
  border-radius: 15px;
  border: none;
  background-color: rgba(168, 198, 134, 1);
  background-color: var(--green);
}

.bookcard a{
  color: white;
  text-decoration: none;
}

.bookcard div:nth-child(3){
  padding: 8px;
  margin: auto;
  position: relative;
  height: 45px;
}


/* Book's details */

.bookdetail{
  padding-top: 110px
}

.bookDetailCard{
  width: 90%;
  margin: auto;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  display: flex;
  height: auto;
  padding: 20px;
}

/* Book's details left side*/

.bookDetailCard>div:nth-child(1){
  width: 25%;
  padding: 1% 3% 0;
}

.bookDetailCard>div:nth-child(2){
  width: 35%;

}

.bookDetailCard>div:nth-child(3){
  padding: 15px;
  width: 35%;
  text-align: justify;
}

.bookDetailCard img{
  width: 100%;
  height: 58vh;
  object-fit: cover;
  border-radius: 15px;
  margin-bottom: 10px;
}

.bookDetailCard>div:nth-child(1)>div:nth-child(2){
  display: flex;
  justify-content: space-around;
}

.bookDetailCard div:nth-child(1) button{
  background-color: #588157;
  background-color: var(--greendark);
  border: none;
  border-radius: 15px;
  padding: 10px;
  color: white;
  margin: 0 15px;
}

.bookDetailCard div:nth-child(1) button:nth-child(2){
  background-color:rgb(167, 29, 49);
  background-color:var(--red);
}

.bookDetailCard div:nth-child(1) a{
  text-decoration: none;
  color: white
}

/* Book's details right side*/

.bookDetailCard h2{
  font: 35px 'Baloo Tammudu 2', cursive;
  margin: 0;
  height: 60px;
  margin-top: 10px;
  line-height: 1;
}

.bookDetailCard h3{
  margin: 0;
  font-size: 25px;
}

.bookDetailCard p{
  font-size: 20px;
}

/* Notes book's card */
.notesbook{
  padding: 3vw;
}

.notesbook button{
  background-color: none;
  border: none
}

.notesbook a{
  background-color: rgba(168, 198, 134, 1);
  background-color: var(--green);
  padding: 10px;
  margin: 2px;
  border-radius: 10px 10px 0 0;
  color: white;
  text-decoration: none;
}

.notescards{
  margin: 10px 2px;
  border: solid rgba(168, 198, 134, 1) 5px;
  border: solid var(--green) 5px;
  border-radius: 0 15px 15px 15px;
  min-height: 10vh;
  padding: 20px;
}

.onenotecard{
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  padding: 10px 25px;
  margin-top: 10px;
  text-align: justify;
}

.onenotecard button{
  background-color:rgb(167, 29, 49);
  background-color:var(--red);
  color: white;
  padding: 10px;
  border-radius: 15px;
  width: 10vw;
  margin-left: 70vw;
}

/* Format for new note and quote */

.newnoteformat button{
  background-color:#588157;
  background-color:var(--greendark);
  color: white;
  padding: 10px;
  border-radius: 15px;
  width: 15vw;
}

.newnoteformat form{
  display: flex;
  width: 100%;
  flex-direction: column;
  margin-top: 20px;
}

.newnoteformat textarea{
  width: 95%;
  font: 16px 'Nunito Sans', sans-serif;
  margin: 10px 0;
}

.newnoteformat input{
  margin: 0 10px;
  width: 60%;
  font: 16px 'Nunito Sans', sans-serif;
}

.newnoteformat form>div:nth-child(3){
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.newnoteformat form>div:nth-child(3) div{
  width: 50%;
}

.newnoteformat form>div:nth-child(4),
.newquote>div{
  align-self: flex-end;
}

.newnoteformat form button:nth-child(2){
  background-color: rgb(167, 29, 49);
  background-color: var(--red);
  margin: 0 4vw;
}

.newquote>input{
  margin: 10px 0;
  width: 50%;
}


/* New book */

.newbook{
  padding-top: 60px
}

/* Edit book */

.editbook{
  padding-top: 60px
}

/* Edit user */

.userProfile{
  padding-top: 120px
}

